<template>
<table
  :class="tableClasses"
  :summary="summary"
>
  <caption>
    <slot name="caption">
      {{caption}}
    </slot>
  </caption>
	<thead>
		<tr>
				<th
          v-for="col in headers"
          :key="col.label ? col.label : col"
          scope="col"
        >
          {{ col.label }}
        </th>
		</tr>
	</thead>
  <tbody>
    <tr
      v-for="row in rows"
      :key="row"
    >
      <slot name="row" :row="row">
        <td
          v-for="(cell, i) in row"
          :key="i"
          :scope="(colHeading && i == 0) ? 'row' : false"
          v-html="cell"
        >
        </td>
      </slot>
    </tr>
  </tbody>
</table>
</template>

<script>
    export default {
        props: {
            colHeading: {
              type: Boolean,
              default: false
            },
            tableId: {
                type: String,
                default: 'base-table'
            },
            variant: {
              type: String,
              validator(variant) {
                return ['striped', 'compact'].includes(variant)
              },
              default: ''
            },
            hover: {
                type: Boolean,
                default: true
            },
            striped: {
                type: Boolean,
                default: false
            },
            hasFixedLayout: {
                type: Boolean,
                default: false
            },
            bordered: {
                type: Boolean,
                default: true
            },
            headers: {
                type: [Array, Object],
                default: () => []
            },
            rows: {
                type: Array,
                default: () => []
            },
        },
        methods: {
          handleSort(field) {
            // eslint-disable-next-line no-console
            console.log('sort not handled for field', field)
          }
        },
        computed: {
            tableClasses() {
              const classes = ['bux-table']
              const variantClass = {[`bux-table--${this.variant}`]: this.variant}
              classes.push(variantClass)
              if(this.hasFixedLayout) {
                classes.push('fixed')
              }
              return classes
            },
            columns(){
                if( Array.isArray(this.headers)){
                    return this.headers.map(c => {
                    const column = {...c}
                    column.order = column.sortable ? 'both' : null
                    return column
                    })
                }
                const columns = []
                Object.keys(this.headers).forEach( key => {
                    const column = {...this.headers[key]}
                    column.order = column.order ? column.order : null
                    columns.push(column)
                })

                return columns
            }
        }
    }
</script>

<style lang="scss" >
.bux-table {
  font-family: $font-heading;
  border-collapse: collapse;
}

.fixed {
  table-layout: fixed;
}

// import the required BUX base sass
    @import '../../assets/buxBase.scss';

// import the component specific sass
    @import '@osu/bux-source/src/components/table/_table.scss';
</style>

